import {InferActionsType} from "../store";

type DataMessagesType = {
  key: number
  name: string
  image: string
  message: string
}
export type MessagesStateType = {
  data: Array<DataMessagesType>
}
type ActionsType = InferActionsType<typeof actionCreators>

const initialState: MessagesStateType = {
  data: [
    {
      key: Math.random(),
      name: "Slava",
      image: "https://html5css.ru/w3images/avatar2.png",
      message: "Hello. How are you?",
    },
    {
      key: Math.random(),
      name: "Lesha",
      image: "data:image/jpeg;base64,",
      message: "How are you?",
    },
    {
      key: Math.random(),
      name: "Sveta",
      image: "data:image/jpeg;base64,",
      message: "I'm reading",
    },
    {
      key: Math.random(),
      name: "Vika",
      image: "https://html5css.ru/w3images/avatar2.png",
      message: "What do you do?",
    },
    {
      key: Math.random(),
      name: "Tonya",
      image: "https://html5css.ru/howto/img_avatar2.png",
      message: "Hello",
    }
  ],
};

export const messagesReducer = (state = initialState, action: ActionsType): MessagesStateType => {
  switch (action.type) {
    case "SN/MESSAGES/SET-MESSAGE":
      return {
        ...state,
        data: [...state.data, {
          key: Math.random(),
          name: action.name,
          image: "https://html5css.ru/w3images/avatar2.png",
          message: action.message
        }],
      };
    default:
      return state;
  }
};

export const actionCreators = {
  setMessage: (name: string, message: string) => ({
    type: "SN/MESSAGES/SET-MESSAGE",
    name,
    message,
  } as const)
};
